<template>
    <bk-dialog
        :quick-close="false"
        :has-header="true"
        :ext-cls="'common-dialog'"
        :title="i18n.title"
        width="600"
        padding="30px"
        :is-show.sync="isAuthorityDialogShow"
        @confirm="onAuthorityConfirm"
        @cancel="onAuthorityCancel">
        <div slot="content" v-bkloading="{isLoading: loading || pending, opacity: 1}">
            <div class="common-form-item">
                <label>{{i18n.createTaskAuth}}</label>
                <div class="common-form-content">
                    <bk-selector
                        :list="personGroupList"
                        :selected.sync="createdTaskPerList"
                        :multi-select="true"
                        :has-children='true'
                        :searchable="true"
                        :allow-clear="true">
                    </bk-selector>
                </div>
            </div>
            <div class="common-form-item">
                <label>{{i18n.modifyParamsAuth}}</label>
                <div class="common-form-content">
                    <bk-selector
                        :list="personGroupList"
                        :selected.sync="modifyParamsPerList"
                        :multi-select="true"
                        :has-children='true'
                        :searchable="true"
                        :allow-clear="true">
                    </bk-selector>
                </div>
            </div>
            <div class="common-form-item">
                <label>{{i18n.executeTaskAuth}}</label>
                <div class="common-form-content">
                    <bk-selector
                        :list="personGroupList"
                        :selected.sync="executeTaskPerList"
                        :multi-select="true"
                        :has-children='true'
                        :searchable="true"
                        :allow-clear="true">
                    </bk-selector>
                </div>
            </div>
        </div>
    </bk-dialog>
</template>
<script>
import { mapActions } from 'vuex'
import { errorHandler } from '@/utils/errorHandler.js'
export default {
    name: 'AthorityManageDialog',
    props: ['isAuthorityDialogShow', 'templateId', 'pending'],
    data () {
        return {
            loading: true,
            personGroupList: [],
            createdTaskPerList: [],
            modifyParamsPerList: [],
            executeTaskPerList: [],
            i18n: {
                title: gettext('权限管理'),
                createTaskAuth: gettext('新建任务权限'),
                modifyParamsAuth: gettext('认领任务权限'),
                executeTaskAuth: gettext('执行任务权限')
            }
        }
    },
    created () {
        this.loadData()
    },
    methods: {
        ...mapActions('templateList/', [
            'getBizPerson',
            'getTemplatePersons'
        ]),
        async loadData () {
            this.loading = true
            try {
                Promise.all([
                    this.loadBizPerson(),
                    this.loadTemplatePersons()
                ]).then(values => {
                    this.personGroupList = values[0].roles.map(item => {
                        return {
                            name: item.text,
                            children: item.children.map(i => {
                                return {
                                    id: i.id,
                                    name: i.text
                                }
                            })
                        }
                    })
                    this.createdTaskPerList = values[1].create_task_groups.map(item => item.show_name)
                    this.modifyParamsPerList = values[1].fill_params_groups.map(item => item.show_name)
                    this.executeTaskPerList = values[1].execute_task_groups.map(item => item.show_name)
                    this.loading = false
                })
            } catch (e) {
                errorHandler(e, this)
            }
        },
        async loadBizPerson () {
            try {
                const res = await this.getBizPerson()
                if (res.result) {
                    return res.data
                } else {
                    errorHandler(res, this)
                    return []
                }
            } catch (e) {
                errorHandler(e, this)
            }
        },
        async loadTemplatePersons () {
            try {
                const res = await this.getTemplatePersons(this.templateId)
                if (res.result) {
                    return res.data
                } else {
                    errorHandler(res, this)
                    return []
                }
            } catch (e) {
                errorHandler(e, this)
            }
        },
        onAuthorityConfirm () {
            const data = {
                templateId: this.templateId,
                createTask: this.createdTaskPerList,
                fillParams: this.modifyParamsPerList,
                executeTask: this.executeTaskPerList
            }
            this.$emit('onAuthorityConfirm', data)
        },
        onAuthorityCancel () {
            this.$emit('onAuthorityCancel')
        }
    }
}
</script>
<style lang="scss" scoped>
.common-form-item {
    label {
        font-weight: normal;
    }
}
</style>


